$paddingleft: 25px;
$fontcolor: whitesmoke;
aside {
    position: absolute;;
    top: 0;
    left: 0;
    bottom: 0;
    width:350px;
    background-color: #333;
    z-index: 10;
    padding: 0 ;
    -webkit-transition: all 1s ease;
     -moz-transition: all 1s ease;
     -o-transition: all 1s ease;
     -ms-transition: all 1s ease;
     transition: all 1s ease;

}
.hideaside{
  left: -360px;
}
.showaside{
    left: 0 !important;
    background-color: #333;
    z-index: 10;
}
.navelement{
  width: 90%;
  margin: 0 auto;
  margin-bottom: 10px;
  font-weight: bold;
}

.navelement p{
  padding: 0 10px;
  margin-right: 5px;
}

.navitem:hover{
  background: #333;
}
.navelement::after{
  border-bottom: 3px solid #ccc;
  display: block;
  margin: 0 auto;
  content: '';

}
.navelement::before{
  border-top: 3px solid #ccc;
  display: block;
  margin: 0 auto;
  content: '';

}
.NavHeaders,.NavHeaders a:link,.NavHeaders a:visited{
  display: block;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ff9900;
  text-shadow: rgba(0, 0, 0, 0.699219) 1px 1px 0px;
  text-decoration: underline;
  padding: 5px 0;
  cursor: pointer;

}
.SubNavHeaders{
    font-size: 18px;
    display: block;
    width: 100%;
    text-align: center;
    color: $fontcolor;
    margin: 0;
    padding: 5px 0;

}
aside a:link,aside a:visited{
  color: $fontcolor;
  text-decoration: none !important;
}
.articleSummary{
  color: 	#5ba1cf;
  display: block;
  margin: 0 auto;
  width: 100%;
  font-size: 15px;

}
#payment{
  width: 100%;
}

aside p {
  font-size: 17px;
  line-height: 26px;
  letter-spacing: 1px;
  position: relative;
  max-height: 800px;
  overflow: hidden;
  opacity: 1;
  transform: translate(0, 0);
  z-index: 2;
}

ul {
  list-style: none;
  perspective: 900;
  padding: 0;
  margin: 0;
}
ul li {
  position: relative;
  padding: 0;
  margin: 0;
}
ul li:nth-of-type(1) {
  animation-delay: 0.5s;
}
ul li:nth-of-type(2) {
  animation-delay: 0.75s;
}
ul li:nth-of-type(3) {
  animation-delay: 1s;
}
ul li:last-of-type {
  padding-bottom: 0;
}
.check{
  position: absolute;
  transform: translate(-6px, 0);
  margin-top: 16px;
  right: 0;
}
 .check:before, .check:after {
  content: "";
  position: absolute;
  background-color: #99cc00;
  width: 3px;
  height: 9px;
}
.check:before {
  transform: translate(-2px, 0) rotate(45deg);
}
.check:after {
  transform: translate(2px, 0) rotate(-45deg);
}
ul li input[type=checkbox] {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
}
ul li input[type=checkbox]:checked ~ p {
  margin-top: 0;
  max-height: 0;
  opacity: 0;
  transform: translate(0, 50%);
}
ul li input[type=checkbox]:checked ~ i:before {
  transform: translate(2px, 0) rotate(45deg);
}
ul li input[type=checkbox]:checked ~ i:after {
  transform: translate(-2px, 0) rotate(-45deg);
}

@keyframes flipdown {
  0% {
    opacity: 0;
    transform-origin: top center;
    transform: rotateX(-90deg);
  }
  5% {
    opacity: 1;
  }
  80% {
    transform: rotateX(8deg);
  }
  83% {
    transform: rotateX(6deg);
  }
  92% {
    transform: rotateX(-3deg);
  }
  100% {
    transform-origin: top center;
    transform: rotateX(0deg);
  }
}
#MMinimize{
  margin-bottom: 15px;
}
.flip-horizontal{
    -moz-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    transform: scaleX(-1);
    -ms-filter: fliph; /*IE*/
    filter: fliph; /*IE*/
}

@media only screen
and (min-width: 50px)
and (max-width: 593px)
  {
    body {
        background-position: -360px 0,center ;
    }
    #rightfixed,#scrolltop{
        display: none;
    }
    .articleIcon{
      display: none;
    }
    #MMinimize{
      background-color: transparent!important;
    }
    #fixed{
      position: absolute;
      left: -20px;
      top:-3px ;
       z-index: 15 ;
    }
    aside {
        left: -360px;
    }
    .fixed2{
      left: 0 !important;
    }
    .MSide{
      display: block;
    }
    .articleIcon{
      opacity: 1;
      border: none;
    }
    .articleIcon:hover{
      opacity: 1;
      background-color: #333;
      border-radius: 50%;
      border: none;
    }
    #rightside{
      left: 0px;
      min-width: 320px !important;
      width: 100%;
    }
}
@media only screen
and (min-width: 593px)
and (max-width: 1200px)  {
    body {
        background-position: -360px 0,center;

    }
    aside {
      left: -360px;
      z-index: 10;
    }
    .articleIcon{
      opacity: 1;
      background-color: #333;
    }
    .articleIcon:hover{
      border: 1px solid #5ba1cf;
      background-color: #333;
      opacity: 1;

    }
    #fixed{
      position: absolute;
        left: 15px;
        z-index: 9;
    }
    .MSide{
      display: block;
    }
    #rightside{
      left: 90px ;
      min-width: 400px;
    }
    #optional{
      display: none;
    }
}
.codeblock{
  display: none;
}
.comment{
  white-space:normal;
}
